import React from 'react';

//      
interface WelcomeProps {
    username?: string;
    isConnected: boolean;
}

export const Welcome: React.FC<WelcomeProps> = ({ username, isConnected }) => {
    return (
        <div className="welcome-card card">
            <h1 className="welcome-title">
                {username ? `, ${username}!` : ' !'}
            </h1>
            <p className="welcome-subtitle">
                {isConnected
                    ? '  TON  '
                    : '   TON    Telegram '}
            </p>
            {!isConnected && (
                <p>   " " .</p>
            )}
        </div>
    );
}; 